import { ConnexionAdminComponent } from './connexion-admin/connexion-admin.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardAdminComponent } from './connected/dashboard-admin/dashboard-admin.component';
import { MainDashboardAdminComponent } from './connected/dashboard-admin/main-dashboard-admin/main-dashboard-admin.component';
import { ListClientsAdminComponent } from './connected/dashboard-admin/list-clients-admin/list-clients-admin.component';
import { BloquerClientsAdminComponent } from './connected/dashboard-admin/bloquer-clients-admin/bloquer-clients-admin.component';
import { StatistiqueClientsAdminComponent } from './connected/dashboard-admin/statistique-clients-admin/statistique-clients-admin.component';
import { DemandeClientsAdminComponent } from './connected/dashboard-admin/demande-clients-admin/demande-clients-admin.component';
import { ListModelsAdminComponent } from './connected/dashboard-admin/model/list-models-admin/list-models-admin.component';
import { BloquerModelsAdminComponent } from './connected/dashboard-admin/model/bloquer-models-admin/bloquer-models-admin.component';
import { AskModelsAdminComponent } from './connected/dashboard-admin/model/ask-models-admin/ask-models-admin.component';
import { StatistiqueModelsAdminComponent } from './connected/dashboard-admin/model/statistique-models-admin/statistique-models-admin.component';
import { DetailModelAdminComponent } from './connected/dashboard-admin/model/detail-model-admin/detail-model-admin.component';
import { PaiementAdminComponent } from './connected/dashboard-admin/administration/paiement-admin/paiement-admin.component';
import { ListPaiementAdminComponent } from './connected/dashboard-admin/administration/list-paiement-admin/list-paiement-admin.component';
import { AuthAdminGuardService } from '../../services/auth/auth-admin-guard.service';
import { MessageAdminComponent } from './connected/dashboard-admin/administration/message-admin/message-admin.component';
import { SettingAdminComponent } from './connected/dashboard-admin/administration/setting-admin/setting-admin.component';

const routes: Routes = [
  {
    path: '',
    component: DashboardAdminComponent,
    // canActivate: [AuthAdminGuardService],
    children : [
      {
        path: '',
        component: MainDashboardAdminComponent,
      },
      {
        path: 'dashboard',
        component: MainDashboardAdminComponent
      },
      {
        path: 'les-clients',
        component: ListClientsAdminComponent
      },
      {
        path: 'les-clients-bloques',
        component: BloquerClientsAdminComponent
      },
      {
        path: 'les-demandes-clients',
        component: DemandeClientsAdminComponent
      },
      {
        path: 'clients-statistiques',
        component: StatistiqueClientsAdminComponent
      },
      {
        path: 'les-modeles',
        component: ListModelsAdminComponent
      },
      {
        path: 'les-modeles-bloques',
        component: BloquerModelsAdminComponent
      },
      {
        path: 'les-demandes-modeles',
        component: AskModelsAdminComponent
      },
      {
        path: 'modeles-statistiques',
        component: StatistiqueModelsAdminComponent
      },
      {
        path: 'detail-model',
        component: DetailModelAdminComponent
      },
      {
        path: 'list-paiement',
        component: ListPaiementAdminComponent
      },
      {
        path: 'paiement',
        component: PaiementAdminComponent
      },
      {
        path: 'message-admin',
        component: MessageAdminComponent
      },
      {
        path: 'setting-admin',
        component: SettingAdminComponent
      }
    ]
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }

